div {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #001f54;
}

.board-1 > div {
  display: flex;
}

.board-2 > div {
  display: flex;
}

.board-1 > div > div {
  width: 2.5rem;
  height: 2.5rem;
}
.board-2 > div > div {
  width: 2.5rem;
  height: 2.5rem;
}

.empty {
  border: 1px solid #ffffff;
  background-color: #87ceeb;
}

.ship {
  border: 2px solid #ffffff;
  background-color: #2f4f4f;
}

.miss {
  border: 1px solid #ffffff;
  background-color: #4682b4;
}

.hit {
  border: 1px solid #ffffff;
  background-color: #ff4c4c;
}

.game {
  display: flex;
  gap: 7rem;
  margin-top: 6rem;
  justify-content: center;
}

.empty:hover,
.ship:hover {
  background-color: #3b82f633;
}

.destroy {
  background-color: #8b0000;
  border: 1px solid #ffffff;
}

.winner {
  font-size: 1.5rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  margin-top: 2rem;
  color: white;
}

p,
h3,
h1,
h2,
div,
h4 {
  color: white;
}

h2 {
  font-size: 3rem;
  text-align: center;
  padding: 1rem;
}

.intro {
  font-size: 1.5rem;
  padding: 1.5rem;
}

.rules {
  margin-top: 3rem;
  padding: 1.5rem;
}
h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.rules > div {
  font-size: 1.5rem;
  margin: 0.6rem;
}

.player2,
.bot {
  padding: 0.7rem;
  width: 7rem;
  border-radius: 10px;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 3rem;
  margin-top: 2rem;
}

.end {
  font-size: 2rem;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}
